body, html {
    height: 100%;
    margin: 0;
}

.Background {
    background: url(/static/media/background2.7c54f8a7.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;

    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (max-width: 767px) {
  #music {display: none;}
}

@media screen and (min-width: 768px) {
  #music {display: block;}
}
